const management={
    state: {
        invoiceId:null, //门店对佣单ID 
        InvoiceDetail:null,//门店详情
        companyList:null,//云经纪公司列表
        companyDetail:null,//门店公司详情
        bankList:null//所有银行
    },
    mutations: {
        // 门店对佣单ID 
        invoiceIdFn(state,data){
             state.invoiceId=data
        },
        InvoiceDetail(state,data){
             state.InvoiceDetail=data
        },
        companyListFn(state,data){
            state.companyList=data
        },
        bankList(state,data){
            state.bankList=data
        }
    },
    actions: {
        //门店对佣单ID 
        invoiceIdFn({commit},data){
            commit('invoiceIdFn',data)
        },
        //门店详情
        InvoiceDetail({commit},data){
            commit('InvoiceDetail',data)
        },
        //云经纪公司列表
        companyList({commit},data){
            commit('companyListFn',data)
        },
        //所有银行
        bankList({commit},data){
           commit('bankList',data)
        }
    }
}

export default management;