// system
const router = new VueRouter({
  mode:'hash',
  scrollBehavior: () => ({ y: 0 }),
  isAddDynamicMenuRoutes: false, // 是否已经添加动态(菜单)路由
  routes:[...globalRoutes]
})

// router.beforeEach((to,from,next)=>{
//   if (to.matched.some(res => res.meta.requireAuth)) {
//   if(!store.getters.userinfo.account){
//       next('/login')
//    }else{
//      next()
//    }}else{
//     next()
//    }
// })


router.beforeEach((to, from, next) => {
  if (store.getters.userinfo.account) {
    next()
    // next('/login')
     if(to.path=='/login'){
       next('/')
     }
    if (!router.options.isAddDynamicMenuRoutes) { //是否已经添加了动态路由
       router.options.isAddDynamicMenuRoutes = true
       yunUserAdminMenu().then(res => {
        if (res.status === 200&&res.data.code=== '000' &&res.data.msg==='success') {
          // let routerdata = filterAsyncRouter(data.menuList) //后台拿到路由进行过滤
          let routerdata = filterAsyncRouter(res.data.bizData.items) //后台拿到路由进行过滤
          store.dispatch('navlist', routerdata) //后台拿到路由
          saveObjArr('router', res.data.bizData.items) //存储路由到localStorage
          routerGo(to, next, routerdata) //执行路由跳转方法
        }
      })
    }

  } else {
    if (to.path === '/') {
      next('/login')
     
    } else {
      next()
    }
  }
})


// //递归路由实现
function filterAsyncRouter(data, routerArr = []) {
  for (var i = 0; i < data.length; i++) {
    if (data[i].type === 0) {
      for (var j = 0; j < data[i].childMenus.length; j++) {
        let childarr = []
        //判断有三级路由没
        if (data[i].childMenus[j].childMenus && data[i].childMenus[j].childMenus.length >= 1) {
          childarr = filterAsyncRouter(data[i].childMenus[j].childMenus)
        }
        //判断有三级路由没
        let temRouter = {}
        // console.log(data[i].list[j].url)
        temRouter.path = data[i].childMenus[j].url
        temRouter.name = data[i].childMenus[j].url.split('/')[2]
        temRouter.meta = {
          name: data[i].childMenus[j].name,
          iframeUrl: "",
          isDynamic: true,
          isTab: true,
          menuId: data[i].childMenus[j].menuId,
          layout:'layout'
        }
        if (childarr.length >= 1) {
          temRouter.children = childarr
          temRouter.path = '/'+data[i].childMenus[j].url.replace(/\//g, '-')
          temRouter['component'] =Layout
        } else {
          temRouter['component'] =_import(data[i].childMenus[j].url)
        }
        routerArr.push(temRouter)
      }

    }
  }
  // console.log(routerArr)
  return routerArr;

}

function routerGo(to, next,routerdata) {
  //模板
const routes = [
  {
    path: '/',
    // name: 'Home',
    component: Layout,
    children:[
      {
      path:'*',
      redirect:'/404'
    }], 
  }
]
  // let yy=JSON.stringify(routerdata)
  // let loal=JSON.parse(yy)
  // console.log(loal)
  // console.log(store.getters.nav)
  routes[0].children=[{path:'/',redirect:store.getters.nav[0].path},...store.getters.nav,...routes[0].children] //插入模板主页
  let data=[...routes]
  store.dispatch('leftNvaFn',data)
  router.addRoutes(data) //动态添加路由
  // console.log(router)
  // next()
  // global.antRouter = routerdata //将路由数据传递给全局变量，做侧边栏菜单渲染工作
  next({
    ...to,
    replace: true
  })
}

// //保存路由到本地
function saveObjArr(item, data) {
  localStorage.setItem(item, JSON.stringify(data))
}
export default router
