//成交单
import request from '@/utils/request'

//代理成交单列表
export function agencyTicketList(data){
    return request({
        url:'/ticket/agencyTicketList',
        method:'post',
        data
    })
}
//成交单列表
export function tradeList(data){
    return request({
        url:'/ticket/tradeList',
        method:'post',
        data
    })
}
 

