import { companyList, } from "@/api/business/management"
import { employerAllProject,myProject} from "@/api/business/Picturepreview"
var myMixin = {
  data(){
    return{
      options:[],
      daivalue:'',
      //成交单获取项目楼盘参数传递
      query:{
        pageNo:1,
        pageSize:15,
        status:0,
        keyName:''
       },
    }
  },
  mounted() {
    companyList({}).then(res => { //公司列表
      if (res.data.code === "000") {
        this.$store.dispatch("companyList", res.data.bizData.items);
      }
    });
    //获取所有楼盘员工名字(反向传参)
    employerAllProject({platformId: this.$store.getters.userinfo.departmentId==998?999:998}).then(res=>{
      if(res.data.code==='000'){
        this.$store.dispatch('projectAllFn',res.data.bizData.items)
      }
    })
  },
  methods:{
    myProject(cb){
        myProject(this.query).then(res=>{
        if(res.data.code==='000'){
           this.options=res.data.bizData.items
          let dataarr=this.options.map(item=>{
            item.value=item.projectName
            return item
          })
          cb(dataarr)
        }
      })
  },
  remoteMethods(){
    this.remoteMethod()
  },
  querySearchS(stringval,cb){
      this.query.keyName=stringval
      this.myProject(cb)
  }
  }
}
export default myMixin