const Picturepreview={
    state:{
       projectAll:null,//成交单楼盘列表
       ticlkdeil:null,//成交单详情（单项）
       tickta:null
    },
    mutations:{
        projectAllFn(state,data){
             state.projectAll=data
        },
        PicTickDieal(state,data){
            let dats=JSON.parse(JSON.stringify(data))
            state.ticlkdeil=dats.data
            state.tickta=dats.item
        } 
    },
    actions:{
        //楼盘列表
        projectAllFn({commit},data){
            commit('projectAllFn',data)
        },
        //成交单详情
        PicTickDieal({commit},data){
            commit('PicTickDieal',data)
        }
    }
}
export default Picturepreview;