var myMixin = {
    methods: {
        tableRowClassName({ row, rowIndex }) {
            //操作表格隔行颜色
            if (rowIndex % 2) {
              return "warning-row";
            } else {
              return "success-row";
            }
            return "";
          },
          cellStyle({ row, column, rowIndex, columnIndex }) {
            if (columnIndex === 10) {
              // 指定列号
              return "color:red";
            } else {
              return "";
            }
          },
    }
  }
export default myMixin