import OSS from 'ali-oss'
import {aliyunKey} from '@/api/common/index'
import { Message } from 'element-ui';
let region=null
let accessKeyId=null
let accessKeySecret=null
let bucket=null
let client=null
aliyunKey().then(res=>{
 if(res.data.code==='000'){
     region=res.data.bizData.region.split(':')[1].replace('//','').split('.')[0]
     accessKeyId=res.data.bizData.accessKeyId
     accessKeySecret=res.data.bizData.accessKeySecret
     bucket=res.data.bizData.bucket
    client= new OSS({
      region :region,
      accessKeyId :accessKeyId,
      accessKeySecret :accessKeySecret,
      bucket :bucket
    });
  }
})


//上传
const CooOss = function(file){
    if(this instanceof CooOss){
      let objectKey = file.lastModified+'_'+file.name;
      return new Promise((resolve,reject) => {
        client.put(objectKey, file).then(result =>  {
          resolve({
            code : 0,
            objectKey : objectKey,
            url : this.getOssFileUrl(objectKey),
            msg : "ok"
          });
        }).catch(err => {
          console.error('上传出错了');
          Message.error('上传出错了');
          reject({code : -1 , url : "", objectKey : "", msg : "上传出错了"});
        });
      })
    }
    else{
      return new CooOss(file);
    }
  };

//删除
const DeletOss=function(file){
  if(this instanceof DeletOss){
    // let getOssFileUrl=new CooOss()
    let objectKey=file.lastModified+'_'+file.name;
    return new Promise((resolve,reject) => {
        client.delete(objectKey).then(result =>  {
        resolve(result);
      }).catch(err => {
        console.error('删除出错了');
        Message.error('删除出错了');
        reject({code : -1 , url : "", objectKey : "", msg : "删除出错了"});
      });
    })
  }else{
    return new DeletOss(file);
  }
     

}
  
CooOss.prototype.getOssFileUrl = obecjtKey => {
    if(!obecjtKey) return  new Error("object key 必须传");
    return "https://" + bucket + "." + region + ".aliyuncs.com/" + obecjtKey;

  }
 

export default {
    install(Vue){
      Vue.prototype.Oss = {
        uploadFile : CooOss,
        deleteFile : DeletOss
      };
    }
  };